<template>
	<view class="container">
		<view class="content">
			<u-search placeholder="请输入搜索关键字" disabled shape="round" margin="10px" bgColor="#ffffff" :showAction="false"
				@click="toSearch"></u-search>
			<!-- 头部轮播 -->
			<view class="carousel-section">
				<u-swiper class="swiper" :list="carouselList" keyName="url" circular indicator height="400rpx"
					radius="5"></u-swiper>
			</view>
			<view class="category">
				<u-scroll-list :indicator="indicator" indicatorColor="#fff0f0" indicatorActiveColor="#f56c6c">
					<view v-for="(item, index) in categoryList" :key="index" class="category-item" @click="toCategory">
						<u-image :showLoading="true" src="/static/image/2.jpg" width="50" height="50" shape="circle">
						</u-image>
						<text style="margin-top: 4px;width: 100%;" class="u-line-1">{{item.cateName}}321321321</text>
					</view>
					<view class="category-item" @click="toCategory">
						<u-icon size="50" name="more-circle-fill" color="#2b85e4"></u-icon>
						<text>查看更多</text>
					</view>
				</u-scroll-list>
			</view>

			<!-- 			<view class="ad-1">
				<u-image :showLoading="true" src="/static/temp/ad1.jpg" width="100%" height="210rpx"
					mode="scrollToFill"></u-image>
			</view> -->

			<!-- 折扣 -->
			<view class="hot-header">
				<i class="iconfont icon-xianshitejia" style="font-size:30px;margin-right: 20rpx;color:#fa436a"></i>
				<view class="tit-box">
					<text class="tit">限时折扣</text>
				</view>
				<u-icon name="arrow-right"></u-icon>
			</view>
			<view class="seckill-section">
				<u-scroll-list :indicator="indicator" indicatorColor="#fff0f0" indicatorActiveColor="#f56c6c">
					<view v-for="(item, index) in goodsList" :key="index" class="floor-item"
						@click="navToDetailPage(item)">
						<u-image :showLoading="true" :src="item.image" width="150rpx" height="150rpx" radius="4px">
						</u-image>
						<text class="title u-line-1" style="display: block;font-size:13px;">{{item.title}}</text>
						<view>
							<text class="price">￥0</text>
						</view>
					</view>
				</u-scroll-list>
			</view>

			<!-- 分类精选 -->
			<view class="hot-header">
				<i class="iconfont icon-fenlei" style="font-size:30px;margin-right: 20rpx;color:#fa436a"></i>
				<view class="tit-box">
					<text class="tit">分类精选</text>
				</view>
				<u-icon name="arrow-right"></u-icon>
			</view>
			<view class="category-section">
				<u-scroll-list :indicator="indicator" indicatorColor="#fff0f0" indicatorActiveColor="#f56c6c">
					<view v-for="(item, index) in goodsList" :key="index" class="cate-item"
						@click="navToDetailPage(item)">
						<view class="image-wrapper">
							<u-image :showLoading="true" :src="item.image" width="100%" height="330rpx" radius="4px">
							</u-image>
						</view>
						<text class="title u-line-1" style="display: block;">{{item.title}}</text>
						<view>
							<text class="price">￥{{item.price}}</text>
							<text class="originPrice">￥{{item.price}}</text>
						</view>
					</view>
				</u-scroll-list>
			</view>

			<!-- 推荐 -->
			<view class="hot-header">
				<i class="iconfont icon-remen" style="font-size:30px;margin-right: 20rpx;color:#fa436a"></i>
				<view class="tit-box">
					<text class="tit">热门推荐</text>
				</view>
				<u-icon name="arrow-right"></u-icon>
			</view>

			<view class="hot-section">
				<view v-for="(item, index) in productList" :key="index" class="product-item"
					@click="navToDetailPage(item)">
					<view class="image-wrapper">
						<u-image :showLoading="true" :src="JSON.parse(item.photoPath)[0].url" width="100%"
							height="330rpx" radius="4px">
						</u-image>
					</view>
					<view style="padding:10rpx;">
						<text class="title u-line-1" style="display: block;">{{item.productName}}</text>
						<view class="btm">
							<text class="price">￥{{item.price}}</text>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="login-alert" v-if="!isLogin">
			<view class="left">
				登录国本商城打开精彩世界
			</view>
			<button type="default" class="u-reset-button" @click="toLogin">立即登录</button>
		</view>
		<updateDialog :showModal.sync="showModal"></updateDialog>
	</view>
</template>

<script>
	import updateDialog from '@/components/updateDialog.vue';
	import {
		getAdvertising
	} from '@/api/public.js'
	import {
		getAllProductCate,
		getProductList
	} from '@/api/product.js'
	export default {
		data() {
			return {
				showModal: false,
				timeData: {},
				carouselList: [],
				goodsList: [],
				indicator: true,
				categoryList: [],
				productList: [],
				form: {
					keyword: null,
					categoryId: null,
					page: 1,
					pageSize: 25
				},
				total: 0
			};
		},

		components: {
			updateDialog
		},

		computed: {
			isLogin() {
				return this.$store.getters.isLogin
			}
		},

		onLoad() {
			this.getPageData()
		},
		methods: {
			getPageData() {
				//获取banner
				getAdvertising({
					categoryName: 'home_banner'
				}).then(res => {
					this.carouselList = res.data;
				})
				//获取所有产品分类
				getAllProductCate().then(res => {
					this.categoryList = res.data;
				})
				//获取所有产品
				getProductList(this.form).then(res => {
					this.productList = res.data.list;
					this.total = res.data.total;
				})
				this.goodsList = this.$json.goodsList;
			},
			toLogin() {
				uni.navigateTo({
					url: '/pages/login/login'
				})
			},
			navToDetailPage(item) {
				uni.navigateTo({
					url: '/pages/productDetail/productDetail?id=' + item.id
				})
			},
			toCategory() {
				uni.switchTab({
					url: '/pages/productCategory/productCategory'
				})
			},
			toSearch() {
				uni.navigateTo({
					url: '/pages/search/search'
				})
			},
			onChange(e) {
				this.timeData = e
			}
		},
		onPullDownRefresh() {
			this.getPageData()
			setTimeout(() => {
				uni.stopPullDownRefresh()
			}, 1000)
		}
	}
</script>

<style lang="scss" scoped>
	.login-alert {
		position: fixed;
		bottom: var(--window-bottom);
		left: 0;
		right: 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 40px;
		padding: 0 20rpx;
		background-color: rgba(0, 0, 0, .7);
		color: #fff;

		.left {
			flex: 1;
		}

		button {
			width: 100px;
			height: 30px;
			line-height: 30px;
			margin-left: 20px;
			border-radius: 20px;
			background-color: #fa436a;
			color: #fff;
			text-align: center;
			font-size: 14px !important;
		}
	}

	.container {
		padding: 0 10rpx 10rpx;
	}

	.carousel-section {
		margin-bottom: 20px;
	}

	.category {
		padding: 10px 0;
		background: #fff;
	}

	.category-item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: calc(20% - 20rpx);
		margin: 0 10rpx;
		flex-shrink: 0;
		text-align: center;
	}

	.ad-1 {
		height: 210rpx;
		padding: 10rpx 20rpx;
		background: #fff;
	}

	/* 秒杀专区 */
	.seckill-section {
		margin: 10px 0;
		padding: 0 20rpx;
		background: #fff;

		.s-header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			height: 92rpx;
			line-height: 1;

			.left {
				display: flex;
				align-items: center;
			}

			.tip {
				margin: 0 20rpx 0 40rpx;
			}
		}

		.floor-item {
			width: 150rpx;
			margin-right: 20rpx;

			.title {
				padding: 10rpx 0;
			}

			.price {
				color: $price-color;
			}
		}
	}

	.hot-header {
		display: flex;
		align-items: center;
		height: 100rpx;
		margin: 10px 0 0;
		padding: 10rpx 20rpx;
		background: #fff;

		.tit-box {
			flex: 1;
			display: flex;
			flex-direction: column;
		}

		.tit {
			font-size: 16px;
		}
	}

	.category-section {
		background-color: #fff;

		.cate-item {
			display: flex;
			flex-direction: column;
			width: calc(50% - 20rpx);
			margin: 0 10rpx;
			flex-shrink: 0;

			.title {
				padding: 10rpx 0;
			}

			.image-wrapper {
				width: 100%;
				height: 330rpx;
				overflow: hidden;
			}

			.price {
				line-height: 1;
				color: $price-color;
			}

			.originPrice {
				margin-left: 10rpx;
				text-decoration: line-through;
				color: $u-light-color;
			}
		}
	}

	/* 推荐 */
	.hot-section {
		display: flex;
		flex-wrap: wrap;
		padding: 10rpx;
		background-color: #fff;

		.product-item {
			display: flex;
			flex-direction: column;
			margin: 10rpx;
			width: calc(50% - 20rpx);
			background-color: #fff;
			border-radius: 4px;

			.btm {
				display: flex;
				justify-content: space-between;
				align-items: center;
			}
		}

		.image-wrapper {
			width: 100%;
			height: 330rpx;
			overflow: hidden;
		}

		.title {
			padding: 10rpx 0;
			line-height: 30px;
		}

		.price {
			line-height: 1;
			color: $price-color;
			font-weight: 700;
		}

		.num {
			color: $u-tips-color;
			font-size: 12px;
		}

		.originPrice {
			margin-left: 10rpx;
			text-decoration: line-through;
			color: $u-light-color;
		}
	}
</style>
